import { Component, OnInit } from '@angular/core';
import { RestApiService } from "../shared/rest-api.service";
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-ticket',
  templateUrl: './ticket.component.html',
  styleUrls: ['./ticket.component.css']
})
export class TicketComponent implements OnInit {
  i_ticket = this.actRoute.snapshot.params['i_ticket'];

  Tickets: any = {};
  CorresponcenceData: any = [];

  constructor(
    public restApi: RestApiService,
    public actRoute: ActivatedRoute,
    public router: Router
  )  { }

  ngOnInit() {
  this.loadTicket()
  this.loadCorrespondence()
 }

  loadTicket(){
    this.restApi.getTicket(this.i_ticket).subscribe((data: {}) =>{
      this.Tickets = data;
    })
  }

  loadCorrespondence(){
    this.restApi.getCorrespondence(this.i_ticket).subscribe((data: {}) =>{
      this.CorresponcenceData = data;
    })
  }

 }
