import { Component, OnInit } from '@angular/core';
import { RestApiService } from "../shared/rest-api.service";

@Component({ 
  selector: 'app-my-tickets-admin',
  templateUrl: './my-tickets-admin.component.html',
  styleUrls: ['./my-tickets-admin.component.css']
})
export class MyTicketsAdminComponent implements OnInit {

  Tickets: any = [];
  owner  = JSON.parse(localStorage.getItem('user'));

  constructor(public restApi: RestApiService) { }

  ngOnInit() {

    this.loadTickets();
    
   }
   
   loadTickets(){
    return this.restApi.getTicketByOwner(this.owner.name).subscribe((data: {}) => {
      this.Tickets = data;
    })
   }
}
