import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RestApiService } from "../shared/rest-api.service";
import { User } from '../shared/interfaces';

@Component({
  selector: 'app-ticket-edit',
  templateUrl: './ticket-edit.component.html',
  styleUrls: ['./ticket-edit.component.css']
})
export class TicketEditComponent implements OnInit {

  i_ticket = this.actRoute.snapshot.params['i_ticket'];
  ticketData: any = {};
  priorities: string[] = ["Very Low", "Low", "Medium", "High", "Very High"];
  severities: string[] = ["Uncertain", "Minor", "Medium", "Urgent"];
  statuses: string[] = ["New", "Open", "Stalled", "Closed", "Waiting"];
  owners: any = {};
  requestors: any = {};


  constructor(
    public restApi: RestApiService,
    public actRoute: ActivatedRoute,
    public router: Router,
  ) { 
  }

ngOnInit() {
  this.restApi.getTicket(this.i_ticket).subscribe((data: {}) =>{
    this.ticketData = data;
    })

   this.restApi.getCustomers().subscribe((data: User[]) =>{
     this.requestors = Object.values(data).map(data => data.name);
   }) 

   this.restApi.getAdmins().subscribe((data: User[]) =>{
    this.owners = Object.values(data).map(data => data.name);
  }) 

  }

  updateTicketInfo() {
    if(window.confirm('Are you sure?')){
      this.restApi.updateTicket(this.i_ticket, this.ticketData).subscribe(data => {
        this.router.navigate(['ticket/' + this.i_ticket])
      })
    }
  }
}

