import { Component, OnInit} from '@angular/core';
import { RestApiService } from "../shared/rest-api.service";

@Component({
  selector: 'app-tickets_list-admin',
  templateUrl: './tickets_list-admin.component.html',
  styleUrls: ['./tickets_list-admin.component.css']
})
export class TicketsListAdminComponent implements OnInit { 

  Tickets: any = [];

constructor(public restApi: RestApiService) { }

ngOnInit() {

  this.loadTickets();
  
 }
 
 loadTickets(){
  return this.restApi.getTickets().subscribe((data: {}) => {
    this.Tickets = data;
  })
 }
}