import { Component, OnInit, Input } from '@angular/core';
import { RestApiService } from "../shared/rest-api.service";
import { ActivatedRoute, Router } from '@angular/router';


@Component({
  selector: 'app-comment-create',
  templateUrl: './comment-create.component.html',
  styleUrls: ['./comment-create.component.css']
})
export class CommentCreateComponent implements OnInit {

  i_ticket = this.actRoute.snapshot.params['i_ticket'];
  user = JSON.parse(localStorage.getItem('user'));

  @Input() commentDetails = { text: '', user: this.user.name, i_ticket: this.i_ticket}
  public dataComment;
  constructor(
    public restApi: RestApiService, 
    public actRoute: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit(): void {
  }

  addComment(dataComment) {
    this.restApi.createComment(this.commentDetails).subscribe((data: {}) => {
      this.router.navigate(['/ticket/' + this.i_ticket ])
    })
  }
}
