import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RestApiService } from "../shared/rest-api.service";

@Component({
  selector: 'app-company-edit',
  templateUrl: './company-edit.component.html',
  styleUrls: ['./company-edit.component.css']
})

export class CompanyEditComponent implements OnInit {

  i_company = this.actRoute.snapshot.params['i_company'];
  companyData: any = {};

  constructor(
    public restApi: RestApiService,
    public actRoute: ActivatedRoute,
    public router: Router
  ) { 
  }

  ngOnInit() {
    console.log(this.i_company, this.companyData)

    this.restApi.getCompany(this.i_company).subscribe((data: {}) =>{
      this.companyData = data;
      })
  }

  updateCompanyInfo() {
    if(window.confirm('Are you sure?')){
      this.restApi.updateCompany(this.i_company, this.companyData).subscribe(data => {
        this.router.navigate(['companies/'])
      })
    }
  }

}
