import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { RestApiService } from "../shared/rest-api.service";
import { Company } from '../shared/interfaces';

@Component({
  selector: 'app-user-create',
  templateUrl: './user-create.component.html',
  styleUrls: ['./user-create.component.css']
})
export class UserCreateComponent implements OnInit {

  companies: any = {};
  roles: string[] = ["customer", "admin"];


  @Input() userDetails = { name: '', email: '', role: '', password: '', company: ''}
  public dataUser;
  constructor(
    public restApi: RestApiService, 
    private router: Router,
  ) { }

  addUser(dataUser) {
    this.restApi.createUser(this.userDetails).subscribe((data: {}) => {
      this.router.navigate(['/users'])
    })
  }

  ngOnInit() {
    this.restApi.getCompanies().subscribe((data: Company[]) =>{
      this.companies = Object.values(data).map(data => data.name);
      })
  }

}
