import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse  } from '@angular/common/http';
import { Enterprise, Document, territoryCodes, Permission, Dodatok, Tickets, Correspondence, User, Company } from '../shared/interfaces';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})

export class RestApiService {
  
  // Define API
  apiURL = 'http://localhost:333';

  constructor(private http: HttpClient) {}
  
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  }  

  getTickets(): Observable<Tickets[]>{
    return this.http.get<Tickets[]>(this.apiURL + '/api/tickets')
  } 

  getTicket(i_ticket): Observable<Tickets> {
    return this.http.get<Tickets>(this.apiURL + '/api/tickets/' + i_ticket)
  }  

  createTicket(ticket): Observable<Tickets> {
    return this.http.post<Tickets>(this.apiURL + '/api/tickets', JSON.stringify(ticket), this.httpOptions)
}

  updateTicket(i_ticket, ticketData): Observable<Tickets> {
    return this.http.put<Tickets>(this.apiURL + '/api/tickets/' + i_ticket, JSON.stringify(ticketData), this.httpOptions)
  }

  getTicketByOwner(name): Observable<Tickets> {
    return this.http.get<Tickets>(this.apiURL + '/api/tickets/get_by_owner/' + name)
  }  

  getTicketByCreator(name): Observable<Tickets> {
    return this.http.get<Tickets>(this.apiURL + '/api/tickets/get_by_creator/' + name)
  }  

getCorrespondence(i_ticket): Observable<Correspondence[]>{
  return this.http.get<Correspondence[]>(this.apiURL + '/api/correspondence/tt/' + i_ticket)
} 

getReplies(i_ticket): Observable<Correspondence[]>{
  return this.http.get<Correspondence[]>(this.apiURL + '/api/replies/tt/' + i_ticket)
} 

createComment(ticket): Observable<Correspondence> {
  return this.http.post<Correspondence>(this.apiURL + '/api/comment', JSON.stringify(ticket), this.httpOptions)
}

createReply(ticket): Observable<Correspondence> {
  return this.http.post<Correspondence>(this.apiURL + '/api/reply', JSON.stringify(ticket), this.httpOptions)
}

getUsers(): Observable<User[]>{
  return this.http.get<User[]>(this.apiURL + '/api/user/all')
}

getUser(i_user): Observable<User> {
  return this.http.get<User>(this.apiURL + '/api/user/get_by_id/' + i_user)
}  

getAdmins(): Observable<User[]>{
  return this.http.get<User[]>(this.apiURL + '/api/user/all/admins')
}

createUser(dataUser): Observable<User> {
  return this.http.post<User>(this.apiURL + '/api/user', JSON.stringify(dataUser), this.httpOptions)
}

deleteUser(i_user): Observable<User> {
  return this.http.delete<User>(this.apiURL + '/api/user/' + i_user, this.httpOptions)
}

updateUser(i_user, userData): Observable<User> {
  return this.http.put<User>(this.apiURL + '/api/user/' + i_user, JSON.stringify(userData), this.httpOptions)
}

getCustomers(): Observable<User[]> {
  return this.http.get<User[]>(this.apiURL + '/api/user/all/customers')
}

getCompanies(): Observable<Company[]> {
  return this.http.get<Company[]>(this.apiURL + '/api/companies')
}

getCompany(i_company): Observable<Company> {
  return this.http.get<Company>(this.apiURL + '/api/company/' + i_company)
}

createCompany(company): Observable<Company> {
  return this.http.post<Company>(this.apiURL + '/api/company', JSON.stringify(company), this.httpOptions)
}

updateCompany(i_company, companyData): Observable<Company> {
  return this.http.put<Company>(this.apiURL + '/api/company/' + i_company, JSON.stringify(companyData), this.httpOptions)
}

deleteCompany(i_company): Observable<Company> {
  return this.http.delete<Company>(this.apiURL + '/api/company/' + i_company, this.httpOptions)
}

    handleError(error) {
     let errorMessage = '';
     if(error.error instanceof ErrorEvent) {
       // Get client-side error
       errorMessage = error.error.message;
     } else {
       // Get server-side error
       errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
     }
     window.alert(errorMessage);
     return throwError(errorMessage);
  }
}