import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { RestApiService } from "../shared/rest-api.service";
import { Company } from '../shared/interfaces';

@Component({
  selector: 'app-user-edit',
  templateUrl: './user-edit.component.html',
  styleUrls: ['./user-edit.component.css']
})
export class UserEditComponent implements OnInit {

  roles: string[] = ["customer", "admin"];
  companies: any = {};
  userData: any = {};
  i_user= this.actRoute.snapshot.params['i_user'];

  constructor(
    public restApi: RestApiService,
    public actRoute: ActivatedRoute,
    public router: Router,
  ) { 
  }

  ngOnInit() {
    this.restApi.getUser(this.i_user).subscribe((data: {}) =>{
      this.userData = data;
      })

      this.restApi.getCompanies().subscribe((data: Company[]) =>{
        this.companies = Object.values(data).map(data => data.name);
        })
  }

  updateUserInfo() {
    if(window.confirm('Are you sure?')){
      this.restApi.updateUser(this.i_user, this.userData).subscribe(data => {
        this.router.navigate(['users/'])
      })
    }
  }

}
