import { Component, OnInit} from '@angular/core';
import { RestApiService } from "../shared/rest-api.service";

@Component({
  selector: 'app-tickets_list-user',
  templateUrl: './tickets_list-user.component.html',
  styleUrls: ['./tickets_list-user.component.css']
})
export class TicketsListUserComponent implements OnInit { 

  Tickets: any = [];
  creator  = JSON.parse(localStorage.getItem('user'));

constructor(public restApi: RestApiService) { }

ngOnInit() {

  this.loadTickets();
  
 }
 
 loadTickets(){
  return this.restApi.getTicketByCreator(this.creator.name).subscribe((data: {}) => {
    this.Tickets = data;
  })
 }
}