import { Component, OnInit } from '@angular/core';
import { RestApiService } from "../shared/rest-api.service";

@Component({
  selector: 'app-users-list-admin',
  templateUrl: './users-list-admin.component.html',
  styleUrls: ['./users-list-admin.component.css']
})
export class UsersListAdminComponent implements OnInit {

  Users: any = [];


  constructor(public restApi: RestApiService) { }

  ngOnInit(): void {
    this.loadUsers();
  }

  loadUsers(){
    return this.restApi.getUsers().subscribe((data: {}) => {
      this.Users = data;
    })
   }

   removeUser(i_user){
    if (window.confirm('You are going to remove this user. Are you sure?')){
      this.restApi.deleteUser(i_user).subscribe(data =>{
        this.loadUsers()
      })
    }
  }

}
