import { Component, OnInit } from '@angular/core';
import { RestApiService } from "../shared/rest-api.service";
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-ticket-user',
  templateUrl: './ticket-user.component.html',
  styleUrls: ['./ticket-user.component.css']
})
export class TicketUserComponent implements OnInit {
  i_ticket = this.actRoute.snapshot.params['i_ticket'];

  Tickets: any = {};
  RepliesData: any = [];

  constructor(
    public restApi: RestApiService,
    public actRoute: ActivatedRoute,
    public router: Router
  )  { }

  ngOnInit() {
  this.loadTicket()
  this.loadReplies()
 }

  loadTicket(){
    this.restApi.getTicket(this.i_ticket).subscribe((data: {}) =>{
      this.Tickets = data;
    })
  }

  loadReplies(){
    this.restApi.getReplies(this.i_ticket).subscribe((data: {}) =>{
      this.RepliesData = data;
    })
  }

 }
