import { Component, OnInit, Input } from '@angular/core';
import { RestApiService } from "../shared/rest-api.service";
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-reply-create-user',
  templateUrl: './reply-create-user.component.html',
  styleUrls: ['./reply-create-user.component.css']
})
export class ReplyCreateUserComponent implements OnInit {

  i_ticket = this.actRoute.snapshot.params['i_ticket'];
  user = JSON.parse(localStorage.getItem('user'));

  @Input() replyDetails = { text: '', user: this.user.name, i_ticket: this.i_ticket}
  public dataReply;
  constructor(
    public restApi: RestApiService, 
    public actRoute: ActivatedRoute,
    private router: Router
  ) { }

  ngOnInit(): void {
  }

  addReply(dataReply) { 
    this.restApi.createReply(this.replyDetails).subscribe((data: {}) => {
      this.router.navigate(['/my-ticket/' + this.i_ticket ])

    })
  }

}
